<script>
import { getStatistics, getOrders } from "@/api";
export default {
  name: "PrintdoorsWoo",
  data() {
    return {
      storeData: {},
      searchCondition: {
        per_page: 10,
        page: 1,
      },
      columns: [
        {
          title: "Order ID",
          dataIndex: "uuid",
          align: "left",
        },
        {
          title: "Woo ID",
          dataIndex: "third_order_id",
          align: "left",
        },
        {
          title: "Date",
          dataIndex: "third_order_time",
          align: "left",
        },
        {
          title: "Status",
          dataIndex: "status_text",
          align: "left",
        },
        {
          title: "Total",
          dataIndex: "total_amount",
          align: "left",
        },
      ],
      orderList: [],
      pagination: {
        total: 0,
        pageSize: 10,
        current: 1,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50", "100"],
        showTotal: () => `Total ${this.pagination.total} items`,
      },
      orderCount: {
        orderTotal: 0,
        pending: 0,
        productOnSale: 0,
        productNumber: 0,
      },
    };
  },
  methods: {
    getOrderListMethod() {
      getOrders().then((res) => {
        this.orderList = res.data.data;
        this.pagination.total = res.data.total;
      });
    },
    getStatisticsMethod() {
      getStatistics().then((res) => {
        this.orderCount = { ...res.data };
      });
    },
    changeCurrent(current) {
      this.searchCondition.page = current.current;
      this.searchCondition.per_page = current.pageSize;
      this.pagination.current = current.current;
      this.pagination.pageSize = current.pageSize;
      this.getOrderListMethod();
    },
    connectStoreMethod() {
      // const url =
      //   this.storeData.apiHost +
      //   "/api/woocommerce?name=" +
      //   this.storeData.name +
      //   "&domain=" +
      //   this.storeData.websiteW +
      //   "&code=" +
      //   this.storeData.code;
      const url =
        this.storeData.apiHost +
        "/api/woocommerce?params=" +
        this.storeData.params;
      window.open(url, "_blank");
    },
    goPrintdoors() {
      window.open("https://www.printdoors.com/", "_blank");
    },
  },
  mounted() {
    this.storeData = window.printdoorsStoreData;
    this.getOrderListMethod();
    this.getStatisticsMethod();
  },
};
</script>

