import axios from "@/utils/request";

export function getStatistics() {
  return axios({
    url: "/api/woocommerce/store_info/statistics",
    method: "GET",
  });
}

export function getOrders() {
  return axios({
    url: "/api/woocommerce/store_info/orders",
    method: "GET",
  });
}

export function connectStore(params) {
  return axios({
    url: "/api/woocommerce",
    method: "POST",
    params,
  });
}
